import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/DataIndexPage.vue'
import Detail from '../components/DataDetail.vue'
Vue.use(Router)

export const router = new Router({
 mode: 'history',
 routes: [
   {
     path: '/',
     name: 'index',
     component: Index
   },
  {
    path: '/1',
    name: 'detail',
    component: Detail
  }
]
})